import {mapState} from 'vuex';
import many from '@/components/assets/userspic/many.vue'
import f from "@/application/functions";
import _ from "underscore";

export default {
  name: 'userspic',
  props: {
    users: Array,
    unseen: Number,
    slidesPerView: Number,
    chat: {},
    m_chat: {}
  },

  data: function () {
    return {
      convertedUsers: [],
      pUsers: []
    }
  },

  components: {
    many
  },

  created: () => {

  },

  computed: mapState({
    log: function () {
    },
    auth: state => state.auth,
    info: state => {

    },
    anotherUser: function () {
      var self = this
      var me = f.getmatrixid(this.core.mtrx.client.credentials.userId)
      return _.map(self.convertedUsers, function (user) {
        if (user.id !== me) {
          return user
        }
      }).filter(n => n)
    },
    roomMembers: function () {
      var pUsers = this.$store.state.users
      var self = this
      var my = f.getmatrixid(this.core.mtrx.client.credentials.userId)
      if (this.m_chat._selfMembership === 'invite') {
        var names = _.map(self.chat.members, function (member) {
          return member.name
        })
        this.core.user.usersInfo(names).then(r => {
          this.convertedUsers = r
        }).catch(e => {
          console.log(e, "eeerror name")
        })
        return this.convertedUsers
      }
      if (this.m_chat._selfMembership === 'join') {
        this.convertedUsers = _.map(self.chat.members, function (member) {
          var userID = f.getmatrixid(member.userId)
          return {
              id: userID,
              status: member.membership,
              name: pUsers[userID].name,
              image: pUsers[userID].image,
              source: pUsers[userID].source
            }

        })
      }
      return this.chat.members
    }
  }),

  methods: {},
}
